import React from 'react'
import './index.less'
import NoneData from 'components/NoneData/all-none'
import TitleBar from 'components/topbar'
import connect from 'connect'
import { SearchBar, Button, WhiteSpace, WingBlank, Carousel, List, Grid, Card, Tabs } from 'antd-mobile';
const data1 = Array.from(new Array(4)).map(() => ({
  icon: require('../../images/tj11.png'),
  icon1: require('../../images/tj11.png'),
}));


@connect

class Home extends React.Component {
    constructor(props){
        super(props);
        
        this.state = {
          navTop: false,
          swiper: null,
          value: '美食',
          data: ['1', '2', '3'],
          imgHeight: 176,
           disabled: false,
        }

        this.$tab = null;
        this.offsetTop = 0;
    }

    
    componentDidMount () {
       setTimeout(() => {
             this.setState({
               data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
             });
           }, 100);
    }


      onChange= (value) => {
        this.setState({ value });
      };
      clear = () => {
        this.setState({ value: '' });
      };
      handleClick = () => {
        this.manualFocusInst.focus();
    }

    handleScroll(){
        let sTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;

        if(!this.state.navTop && sTop >= this.offsetTop){
           this.setState({
             navTop: true
           })
        }

        if(sTop < this.offsetTop){
           this.setState({
             navTop:false
           })
        }
      }

    renderTabBar(props) {
      return (<div>
        {({ style }) => <div style={{ ...style, zIndex: 1 }}><Tabs.DefaultTabBar {...props} /></div>}
      </div>);
    }


    render () {
      const tabs = [
        { title: '全部' },
        { title: '待付款' },
        { title: '待发货' },
        { title: '待收货' },
        { title: '待评价' },
      ];
      const data = [
        {
          img: 'https://zos.alipayobjects.com/rmsportal/dKbkpPXKfvZzWCM.png',
          title: 'Meet hotel',
          des: '不是所有的兼职汪都需要风吹日晒',
          value: 0,
        },
      ];
        const Item = List.Item;
        const Brief = Item.Brief;
        const {newsList, newsIndex, recommend, daily, banner, classify} = this.props.state.home
        const params = {
              spaceBetween: 30,
              centeredSlides: true,
              autoplay: {
                delay: 1000,
                disableOnInteraction: true
              },
              pagination: {
                el: '.swiper-pagination',
                clickable: true,
              }
            };
        this.state.swiper && this.state.swiper.slideTo(newsIndex)
        return (
            <div className="orderdetail-container">
            <TitleBar title="订单详情"/>
            
            <List  className="my-list">
              <Item
                thumb={require('../../images/dd_wc.png')}
                onClick={() => {}}
              >
                交易完成
              </Item>
              <Item
                thumb={require('../../images/dd_dshcc.png')}
                multipleLine
                onClick={() => {}}
              >
                已从郑州转运中心发出 <Brief>2018-04-20 10:17</Brief>
              </Item>
              <Item extra="18224528561" align="top"  thumb={require('../../images/dd_dz.png')} multipleLine>
                收货人：翟阳春 <Brief><div  className="address">收货地址：河南省郑州市金水区北林路街道办事处经
              三名筑5栋11楼127室</div></Brief>
              </Item>
            </List>
            {data.map((obj, index) => (
             <div  key={'myordercard' + index} >
              <Card full={true}>
                    <Card.Header
                      thumb="https://gw.alipayobjects.com/zos/rmsportal/MRhHctKOineMbKAZslML.jpg"
                      thumbStyle={{width: '1rem',height: '0.75rem', borderRadius: '0.03rem'}}
                      extra={
                       <div style={{ }}>
                         <div className="destext">{obj.des}</div>
                         <div className="mid"></div>
                         <div><span className="jiatext">¥9900.00</span><span className="numtextdes">x1</span></div>
                       </div>
                       
                      }
                    />
              </Card>
           </div>
            ))}
            <Item multipleLine style={{marginBottom: '0.48rem'}}>
            <div  className="ordernum">订单编号：102154668899678889</div>
            <div  className="ordernum">创建时间：2018-07-12 09：46</div>
            <div  className="ordernum">支付时间：2018-07-12 10：25</div>
            <div  className="ordernum">发货时间：2018-07-12 10：26</div>
            <div  className="ordernum">收货时间：2018-07-12 11：00</div>

                          
            </Item>
            <div className="cardfooter">
                   <Button type="ghost"  style={{ marginRight: '4px' }} className="am-button-borderfix">删除订单</Button>
            </div>
     </div>
        )
    }
}
export default Home


